.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
  padding: 30px;
}

.brand-name {
  margin: 20px;
}

.brand-name span {
  font-style: italic;
  color: rgb(96, 223, 44);
}

.status {
  margin: 20px 20px 40px 20px;
  border: 1px solid #ccc;
  background-color: white;
  color: #090909;
  letter-spacing: 1px;
  font-family: century gothic;
}

.status-header {
  padding: 5px 10px;
  margin: 0;
  color: white;
  background-color: #222121d8;
  text-align: left;
}

.status-content, .accidents-content {
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-size: 12px;
  font-weight: normal;
  border-bottom: 1px solid #ccc;
}

.line-name, .status-desc, .location-name, .update-desc {
  font-size: 14px;
}

.status-content:nth-child(odd){
  background-color: rgba(127, 209, 0, 0.61);
}

.line-name {
  -ms-flex: 0.5 1;
      flex: 0.5 1;
  text-transform: uppercase;
}

.status-desc {
  text-align: right;
  -ms-flex: 0.5 1;
      flex: 0.5 1;
  position: relative;
  top: 7px;
}

.status-reason {
  width: 100%;
}

.update-desc, .update-comments {
  margin-top:0 ;
}

.no-updates{
  padding: 10px;
}



